const RouteAnimation = styled.div`
  position: relative;
  .fadeMenu {
  }
  .fadeMenu-enter {
    opacity: 0;
  }

  .fadeMenu-enter.fadeMenu-enter-active {
    opacity: 1;
    /* transition: opacity 400ms linear 200ms; */
    transition: all 400ms;
  }
  .fadeMenu-exit {
    top: 0;
    left: 0;
    position: absolute;
    opacity: 0;
  }
  .fadeMenu-exit.fadeMenu-exit-active {
    opacity: 0;
    /* transition: opacity 200ms linear; */
    transition: all 400ms;
  }
`;

class Menu extends Component {
  constructor(props) {
    super(props);
    this.state = {
      fixed: false,
      width: 0,
      isTouch: false
    };
    this.menuNav = React.createRef();
    this.handleMenuNav = this.handleMenuNav.bind(this);
  }
  componentDidMount() {
    this.handleMenuNav();
  }

  static getDerivedStateFromProps(nextProps, prevState) {
    if (nextProps.fixed !== prevState.fixed) {
      return { fixed: nextProps.fixed };
    }
    if (nextProps.width !== prevState.width) {
      return { width: nextProps.width };
    }
    return false;
  }

  handleMenuNav = () => {
    const isTouch = 'ontouchstart' in document.documentElement;
    const { width } = this.props;
    this.setState({ fixed: false, width, isTouch });
    const menuNavOffsetTop = this.menuNav.current.offsetTop;
    this.props.menuNavbar(menuNavOffsetTop);
  };

  render() {
    // const { fixed, width } = this.state;
    const { fixed, width, mainNavHeight } = this.props;
    const { isTouch } = this.state;

    const style = {
      width: isTouch ? '100%' : width
    };
    const fixedStyle = {
      width: isTouch ? '100%' : width,
      top: mainNavHeight
    };
    const shouldBeCentered = width < 513 ? false : true;
    // console.log(menuData[0].data.map(item => console.log(item)));
    // console.log(fixed);
    return (
      <>
        <div className="header menuHeader">
          <nav
            ref={this.menuNav}
            style={fixed ? fixedStyle : style}
            // backgroundFixed={fixed}
            // width={width}
            // fixed={fixed}
            // mainNavHeight={mainNavHeight}
            // shouldBeCentered={shouldBeCentered}
            className={
              'menuNav ' +
              (fixed ? 'fixed ' : '') +
              (shouldBeCentered ? 'center ' : '')
            }
            // className={'menuNav ' + (fixed && 'fixed')}
          >
            <div className="seoulContainer slider-wrapper">
              <div className="slider-inner">
                <ul className="slider">
                  {menuNavData.map((item, i) => (
                    <li key={i}>
                      <NavLink
                        to={item.url}
                        activeClassName="menu-nav-active"
                        key={item.id}
                      >
                        {item.name}
                      </NavLink>
                    </li>
                  ))}
                </ul>
              </div>
            </div>
          </nav>
        </div>
        <div className="fullWidthContainer">
          <div className="seoulContainer-m">
            <Route
              render={({ location }) => (
                <RouteAnimation>
                  <TransitionGroup>
                    <CSSTransition
                      // unmountOnExit
                      // className="fadeMenu"
                      classNames="fadeMenu"
                      key={location.key}
                      // timeout={{ enter: 4000, exit: 4000 }}
                      timeout={400}
                    >
                      <Switch location={location}>
                        {menuData.map((item, i) => (
                          <Route
                            key={item.path}
                            exact
                            path={item.path}
                            render={() => (
                              <MenuContents
                                key={item.id}
                                data={item}
                                fixed={fixed}
                              />
                            )}
                          />
                        ))}
                      </Switch>
                    </CSSTransition>
                  </TransitionGroup>
                </RouteAnimation>
              )}
            />
          </div>
        </div>
        <div style={{ minHeight: '100vh' }} className="konto" />
        {/* <div>
          {Array.apply(null, Array(30)).map((item, i) => (
            <h1 key={i}>Test</h1>
          ))}
        </div> */}
      </>
    );
  }
}

export default Menu;
